  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}
.transition {
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-backdrop-filter;
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
  transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-backdrop-filter;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}
.duration-300 {
  transition-duration: 300ms;
}
.ease-in-out {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.underline {
    text-decoration-color: hsl(224, 12%, 88%);
  }
.hover\:cursor-pointer:hover {
  cursor: pointer;
}
.hover\:text-secondary:hover {
  --tw-text-opacity: 1;
  color: hsl(0 0% 23% / var(--tw-text-opacity));
}
.group:hover .group-hover\:cursor-pointer {
  cursor: pointer;
}
.group:hover .group-hover\:text-primary {
  --tw-text-opacity: 1;
  color: hsl(0 0% 5% / var(--tw-text-opacity));
}
@media (prefers-color-scheme: dark) {

  .dark\:bg-darkCode {
    --tw-bg-opacity: 1;
    background-color: hsl(224 25% 8% / var(--tw-bg-opacity));
  }

  .dark\:text-darkPrimary {
    --tw-text-opacity: 1;
    color: hsl(0 0% 100% / var(--tw-text-opacity));
  }

  .dark\:text-darkSecondary {
    --tw-text-opacity: 1;
    color: rgb(186 182 177 / var(--tw-text-opacity));
  }

  .dark\:text-darkTertiary {
    --tw-text-opacity: 1;
    color: rgb(121 118 115 / var(--tw-text-opacity));
  }
  .dark\:underline-dark {
    text-decoration-color: #79767384;
  }
  .group:hover .group-hover\:dark\:text-darkPrimary {
    --tw-text-opacity: 1;
    color: hsl(0 0% 100% / var(--tw-text-opacity));
  }
}
@media (min-width: 640px) {

  .sm\:mr-8 {
    margin-right: 2rem;
  }

  .sm\:justify-start {
    justify-content: flex-start;
  }
}
@media (min-width: 768px) {

  .md\:-ml-7 {
    margin-left: -1.75rem;
  }

  .md\:-mt-3 {
    margin-top: -0.75rem;
  }

  .md\:-mt-32 {
    margin-top: -8rem;
  }

  .md\:ml-2 {
    margin-left: 0.5rem;
  }

  .md\:mt-0 {
    margin-top: 0px;
  }

  .md\:mt-32 {
    margin-top: 8rem;
  }

  .md\:mt-4 {
    margin-top: 1rem;
  }

  .md\:max-h-256 {
    max-height: 256px;
  }

  .md\:max-w-256 {
    max-width: 256px;
  }

  .md\:max-w-container-large {
    max-width: 612px;
  }

  .md\:flex-row {
    flex-direction: row;
  }

  .md\:flex-col-reverse {
    flex-direction: column-reverse;
  }

  .md\:opacity-0 {
    opacity: 0;
  }

  .group:hover .md\:group-hover\:opacity-100 {
    opacity: 1;
  }
}

